import Vue from 'vue'
import Vuex from 'vuex'
import HttpCache from '../common/cache'
Vue.use(Vuex)
const user_account = HttpCache.get("user_account"); // 
const store = new Vuex.Store({
    state: {
        hasLogin: user_account ? true : false, // 
        loginProvider: "",
        openid: user_account['token'] ? user_account['token'] : null, // openid
        userinfo: user_account ? user_account : {}, //  
        params:{}
        
    },
    mutations: {
        login(state, user) {
            state.hasLogin = true;
            state.loginProvider = user.provider || "";
            state.openid = user.token;
            state.userinfo = user;
            HttpCache.put("user_account", user); // 
        },
        logout(state) {
            state.hasLogin = false;
            state.openid = null;
            state.userinfo = [];
            HttpCache.remove("user_account"); // 
        },
        setOpenid(state, openid) {
            state.openid = openid
        },
        save(state, data) {
            var user = state.userinfo;
            for (let i in data) {
                user[i] = data[i];
                if (i == "openid") {
                    state.openid = data[i];
                }
            }
            state.userinfo = user;
            HttpCache.put("user_account", user); // 
        },
        getUseriInfo(state) {
            return HttpCache.get("user_account"); // 
        }
    },
    actions: {
        // lazy loading openid
        getUserOpenId: async function({
            commit,
            state
        }) {
            return await new Promise((resolve, reject) => {
                if (state.openid) {
                    resolve(state.openid)
                } else {
                    uni.login({
                        success: (data) => {
                            commit('login')
                            setTimeout(function() { // openid
                                const openid = '123456789'
                                console.log('uni.request mock openid[' + openid +
                                    ']');
                                commit('setOpenid', openid)
                                resolve(openid)
                            }, 1000)
                        },
                        fail: (err) => {
                            console.log('uni.login ', err)
                            reject(err)
                        }
                    })
                }
            })
        }
    }
})

export default store
